import { request } from '../../util/api.js';
import {url} from "@/util/http.js";
export default {
	data() {
		return {
			billArr: [],
			year: new Date().getFullYear(),
			mounth: new Date().getMonth() + 1 <= 9 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1,
			sort_pages: 5,
			nowPage: 1,
			pageInfo: {},
			openid: '',
			page: 1,
			marked_words: '上拉加载更多'
		};
	},
	onLoad() {
		const openid = uni.getStorageSync('openid');
		if (openid == '' || openid == null) {
			return uni.switchTab({
				url: '../user/user'
			});
		}
	},
	onShow() {
		this.billArr = [];
		this.nowPage = 1;
		this.page = 1;
		const openid = uni.getStorageSync('openid');
		this.openid = openid;
		this.getBill(openid, this.nowPage);
	},
	onReachBottom() {
		if (this.page > this.pageInfo.totalPage) {
			this.marked_words = '已经到底了，别拉了。。。';
		} else {
			this.getBill(this.openid, this.nowPage);
		}
	},
	methods: {
		bindDateChange(e) {
			const { value } = e.detail;
			(this.year = value.split('-')[0]), (this.mounth = value.split('-')[1]);
		},
		getBill(openid, page) {
			uni.showLoading({
				title: '加载中。。。'
			});
			request({
				url: url+'?op=getBill',
				data: { openid, page }
			}).then(resp => {
				console.log(resp);
				const { code, msg } = resp.data;
				if (code === 0) {
					this.billArr = [...this.billArr, ...resp.data.info];
					this.pageInfo = resp.data.pageInfo[0];
					this.nowPage = this.pageInfo.nextPage;
					this.page += 1;
					setInterval(function() {
						uni.hideLoading();
					}, 2000);
				} else {
					uni.showToast({
						title: msg
					});
				}
			});
		},
		lookDetail(e) {
			console.log(e);
		    uni.navigateTo({
		    	url:"../billdetail/billdetail?id="+e,
		    })
		}
	}
};
